.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);

    display: flex;
    justify-content: center;
    align-items: center;

}

.popup-inner {
    position: relative;
    padding: 35px;
    width: 100%;
    max-width: 700px;
    background-color: white;
    border-radius: 5px;
}

.popup-inner .close-btn {
    position: absolute;
    top: 15px;
    right: 15px;
}

.popup-inner .announce-btn {
    position: absolute;
    bottom: 1px;
    right: 15px;
}
